import React from 'react';
import Edit from "../../../Images/Edit.png";
import Delete from "../../../Images/Delete.png";

const  list =  [
    {
        heading: "Lorem Ipsum is simply dummy text of the",
        para: "It is a long established fact that a reader will be distracted by the readable content",
        price: "120 AED",
        edit: `${Edit}`,
        del: `${Delete}`,
    },
]

const MenuList = [...list, ...list, ...list, ...list, ...list, ...list, ...list, ...list, ...list, ...list]
const Menulist = () => {
    return ( 
        <div className="flex flex-col mt-4 flex-wrap">
            {
                MenuList.map(({heading, para, price, edit, del}) => {
                    return (
                        <div className="flex border-2 bg-gray-200 border-white px-4 py-2 items-center justify-between">
                            <div className="">
                                <h1 className="font-bold text-xl">{heading}</h1>
                                <p>{para}</p>
                            </div>
                            <div className="mr-2">
                                <p className="text-red-700 text-lg font-bold">{price}</p>
                            </div>
                            <div className="mt-0">
                                <button>
                                    <img src={edit} alt=""/>
                                </button>
                                <button className="md:ml-3">
                                    <img src={del} alt=""/>
                                </button>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    );
}
 
export default Menulist;