import React, { useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import CheckIcon from '@material-ui/icons/Check';
import StepConnector from '@material-ui/core/StepConnector';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import Step1Form from '../Stepper/Step 1/Step1';
import Step2Form from '../Stepper/Step 2/Step2';
import Step3 from './Step 3/Step3';
import { useHistory } from 'react-router';

const ColorlibConnector = withStyles({
  alternativeLabel: {
    top: 22,
  },
  active: {
    '& $line': {
      backgroundColor:
        '#38D783',
    },
  },
  completed: {
    '& $line': {
      backgroundColor:
        '#38D783',
    },
  },
  line: {
    height: 7,
    border: 0,
    backgroundColor: '#eaeaf0',
    borderRadius: 1,
  },
})(StepConnector);

const useColorlibStepIconStyles = makeStyles({
  root: {
    backgroundColor: '#ccc',
    zIndex: 1,
    color: 'gray',
    width: 50,
    height: 50,
    display: 'flex',
    borderRadius: '50%',
    justifyContent: 'center',
    alignItems: 'center',
  },
  active: {
    backgroundColor:
      '#38D783',
    boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    color:'white',
  },
  completed: {
    backgroundColor:
      '#38D783',
      color:'white',
  },
});

function ColorlibStepIcon(props) {
  const classes = useColorlibStepIconStyles();
  const { active, completed } = props;

  return (
    <div
      className={clsx(classes.root, {
        [classes.active]: active,
        [classes.completed]: completed,
      })}
    >
      <CheckIcon />
    </div>
  );
}

const useStyles = makeStyles(() => ({
  button: {
    borderRadius:'50%',
    width: 50,
    height: 50,
    color: 'white',
    backgroundColor: '#38D783',
    marginRight: '10px',
  },
  buttonBack: {
    borderRadius:'50%',
    width: 50,
    height: 50,
    color: 'black',
    backgroundColor: 'lightgray',
    marginRight: '10px',
  },
  disable:{
    display: 'none',
  },
}));

function getSteps() {
  return ['Owner Details', 'Entity Details', 'Entity Operation Details'];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return <Step1Form/>;
    case 1:
      return <Step2Form/>;
    case 2:
      return <Step3/>;
    default:
      return 'Unknown step';
  }
}

export default function CustomizedSteppers() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = useState(0);
  const steps = getSteps();
  const history = useHistory();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    if(activeStep === steps.length - 1){
        history.push("/admin/promotion");
    }
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  return (
    <div className="mb-20">
      <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />}>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
          <Typography>{getStepContent(activeStep)}</Typography>
          <div className="flex justify-end mr-8">
            <Button disabled={activeStep === 0} onClick={handleBack} className={activeStep === 0 ? classes.disable : classes.buttonBack}>
              <ChevronLeftIcon/>
            </Button>
            <Button
              variant="contained"
              onClick={handleNext}
              className={classes.button}
            >
              <ChevronRightIcon/>
            </Button>
          </div>
        </div>
    </div>
  );
}
