import React, { useRef } from 'react';
import Img from "../../Images/Rectangle177.png";
import Img1 from "../../Images/Rectangle177.png";
import Img2 from "../../Images/Rectangle177.png";
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

const MenuImgs = [
    {
        url:`${Img}`,
    },
    {
        url:`${Img1}`,
    },
    {
        url:`${Img2}`,
    },
]
const MenuSlider = () => {
    const SliderRef = useRef();

    return (
        <div>
           <div>
            <Slider ref={SliderRef}>
                {
                    MenuImgs.map(({url}) => {
                        return(
                            <center>
                                <div>
                                    <img src={url} alt=""/>
                                </div>
                            </center>
                        )
                    })
                }
            </Slider>
            </div> 
            <div className="flex justify-center mt-4">
                {
                    MenuImgs.map(({url}, index) => {
                        return(
                            <div className={`w-32 ml-2 cursor-pointer`} onClick={() => SliderRef.current.slickGoTo(index)}>
                                <img src={url} alt="" className="rounded-lg"/>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    );
}
 
export default MenuSlider;