import React from 'react';
import { Link } from 'react-router-dom';
import Cook from "../../../../Images/asset12.png";

const Title = () => {
    const Btns = [
        {
            name: "Foodie",
            url: "/admin/news",
        },
        {
            name: "Pizza",
            url: "/admin/promotion",
        },
        {
            name: "Pizza",
            url: "/admin/menu",
        },
        {
            name: "Lunch",
            url: "/admin",
        },
        {
            name: "Pizza",
            url: "/admin/menulist",
        },
    ]
    return ( 
        <div className="bg-gray-200">
            <div className="flex justify-between flex-wrap px-16 py-8">
                <div className="bg-white rounded-full h-56 w-56 flex justify-center items-center">
                    <img src={Cook} alt="" className="w-40 h-40"/>
                </div>
                <div className="flex items-center flex-wrap">
                    {
                        Btns.map(({name, url}) => {
                            return (
                                <div key={url}>
                                    <Link to={url} className="p-2 m-4 rounded-full bg-white w-32">{name}</Link>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    );
}
 
export default Title;