import React from 'react';
import Burger from "../../../../Images/image10.png";
import SampleImg from "../../../../Images/SampleImg.png";

const Images = (props) => {
    return ( 
        <div className="mt-4">
            <div className="mb-4">
                <h1 className="text-red-600 text-2xl font-bld">{props.name}</h1>
            </div>
            <div className="flex">
                <div>
                    <img src={Burger} alt=""/>
                </div>
                <div className="bg-gray-200 w-56 ml-2 flex flex-col justify-center items-center rounded-xl p-8">
                    <div className="mb-2"> 
                        <img src={SampleImg} alt=""/>
                    </div>
                    <div>
                        <h2 className="font-bold">Add Image</h2>
                    </div>
                    <div>
                        <p className="text-gray-600">Maximum size should not exceed 200 kb</p>
                    </div>
                </div>
            </div>
        </div>
    );
}
 
export default Images;