import React from 'react';

const Form1 = (props) => {
    return ( 
        <div className="flex flex-col">
            <div className="flex flex-wrap md:flex-no-wrap">
                <div className="flex flex-col w-full md:w-1/2">
                    <label htmlFor="nameEng">{props.name} <p className="text-red-600 inline-block">(English)</p> </label>
                    <input type="text" id="nameEng" className="border-2 rounded-xl h-12"/>
                </div>
                <div className="flex flex-col w-full md:w-1/2 md:ml-4">
                    <label htmlFor="nameAra">{props.name} <p className="text-red-600 inline-block">(Arabic)</p> </label>
                    <input type="text" id="nameAra" className="border-2 rounded-xl h-12"/>
                </div>
            </div>
            <div className="flex mt-4">
                <div className="flex flex-col w-full">
                    <label htmlFor="descEng">Description <p className="text-red-600 inline-block">(English)</p></label>
                    <textarea name="" id="descEng" cols="30" rows="10" className="w-full border-2 rounded-xl"></textarea>
                </div>
            </div>
            <div className="flex mt-4">
                <div className="flex flex-col w-full">
                    <label htmlFor="descAra">Description <p className="text-red-600 inline-block">(Arabic)</p></label>
                    <textarea name="" id="descAra" cols="30" rows="10" className="w-full border-2 rounded-xl"></textarea>
                </div>
            </div>
        </div>
    );
}
 
export default Form1;