import React from 'react';
import Form1 from '../Admin/Stepper/ReuseableFrm/Form1';
import Images from '../Admin/Stepper/ReuseableFrm/Images';
import Title from '../Admin/Stepper/ReuseableFrm/Title';

const CreatePromotions = () => {
    return ( 
        <div>
            <Title/>
            <div className="p-16">
                <Images name="Promotions"/>
                <Form1 name="Title"/>
                <div className="flex mt-4 flex-wrap md:flex-no-wrap">
                    <div className="flex flex-col w-full md:w-1/2">
                        <label htmlFor="datefrom">Date <p className="inline-block text-red-600">(From)</p></label>
                        <input type="date" id="datefrom" className="border-2 w-full rounded-lg p-2"/>
                    </div>
                    <div className="flex flex-col w-full md:w-1/2 md:ml-4">
                        <label htmlFor="dateto">Date <p className="inline-block text-red-600">(To)</p></label>
                        <input type="date" id="dateto" className="border-2 w-full rounded-lg p-2"/>
                    </div>
                </div>
                <div className="mt-4">
                    <button className="p-4 bg-red-700 text-white rounded-full w-40 md:w-56 font-bold text-lg">Add Promotions</button>
                </div>
            </div>
        </div>
    );
}
 
export default CreatePromotions;