import React from 'react';
import Footer from "./components/Footer/Footer";
import Header from "./components/Header/Header";
import Amaken from "./components/Pages/Amaken";
import { LanguageContextProvider } from './components/Context/LanguageContext';
import { BrowserRouter as Router , Route , Switch } from 'react-router-dom';
import "./components/app.css";
import Home from './components/Pages/HomePage';
import EventList from './components/Pages/EventList';
import NewsList from "./components/Pages/NewsList"
import EventDetails from './components/Pages/EventDetails';
import MediaMap from './components/Media/MediaMap';
import NewsDetails from './components/Pages/NewsDetails';
import MenuPage from './components/Pages/MenuPage';
import Location from './components/Pages/Location';
import CustomizedSteppers from './components/Admin/Stepper/Stepper';
import CreateNews from './components/Pages/CreateNews';
import CreateMenu from './components/Pages/CreateMenu';
import CreatePromotions from "./components/Pages/CreatePromotion";
import MenuList from './components/Pages/MenuList';

const App = () => {
  return ( 
  <div>
    <Router>
      <LanguageContextProvider>
       <Header/>
        <Switch>
          <Route exact path="/" component={Amaken}/>
          <Route exact path="/home" component={Home}/>
          <Route exact path="/events" component={EventList}/>
          <Route exact path="/news" component={NewsList}/>
          <Route exact path="/events/eventDetails" component={EventDetails}/>
          <Route exact path="/news/newsDetails" component={NewsDetails}/>
          <Route exact path="/menu" component={MenuPage}/>
          <Route exact path="/location" component={Location}/>
          <Route exact path="/admin" component={CustomizedSteppers}/>
          <Route exact path="/admin/news" component={CreateNews}/>
          <Route exact path="/admin/menu" component={CreateMenu}/>
          <Route exact path="/admin/promotion" component={CreatePromotions}/>
          <Route exact path="/admin/menulist" component={MenuList}/>
        </Switch>
      <Footer/> 
      </LanguageContextProvider>
    </Router> 
  </div>
   );
}
 
export default App;