import React from 'react';
import MediaBottom from '../Media/MediaBottom';
import SimpleContent from "../Simple Content/Content";
import SecondContent from "../Simple Content/SecondContent";
import Music from '../Reuseable Components/Adds/Music';
import OnlineBooking from '../Reuseable Components/Adds/OnlineBooking';
import Share from '../Reuseable Components/Adds/Share';
import CommentForm from '../Reuseable Components/Comments/CommentForm';
import Comments from '../Reuseable Components/Comments/Comments';
import Content from '../Reuseable Components/Content/Content';
import Highlights from '../Reuseable Components/Highlights/Highlights';
import Medialeft from '../Reuseable Components/Media/MediaLeft';
import Search from '../Reuseable Components/Search/Search';
import Title from '../Reuseable Components/Title/Title';

const NewsDetails = () => {
    return ( 
        <div>
            <Title/>
            <div className="p-12 flex md:flex-no-wrap flex-wrap">
                <div className="flex flex-col">
                    <MediaBottom/>
                    <Content/><br/>
                    <Medialeft/>
                    <SimpleContent/>
                    <SecondContent/>
                    <Share/>
                    <Comments name="Recent Comments"/>
                    <CommentForm name="Leave A Comments"/>
                </div>
                <div className="flex flex-col ml-4">
                    <Search/><br/>
                    <Music/><br/>
                    <OnlineBooking/><br/>
                    <Highlights name="Highlights:" color="bg-gray-100" round/>
                </div>
            </div>
        </div>
    );
}
 
export default NewsDetails;