const Location = () => {
    return ( 
        <div>
            <Title/>
            <div className="flex flex-wrap md:flex-no-wrap m-8">
                <div className="flex flex-col">
                    <BranchLocation src={`${Img}`}/>
                    <BranchLocation src={`${Img2}`}/>
                    <BranchLocation src={`${Img}`}/>
                </div>
                <div className="bg-gray-600">
                    Add Map Here
                </div>
            </div>
        </div>
    );
}
 
export default Location;