import React from 'react';
import SampleImg from "../../../../Images/SampleImg.png";

const StepImage = () => {
    return ( 
        <div className="flex flex-wrap md:flex-no-wrap">
            <div className="bg-gray-200 flex flex-col justify-center items-center rounded-xl p-8">
                <div className="mb-2">
                    <img src={SampleImg} alt=""/>
                </div>
                <div>
                    <h1 className="text-2xl font-bold">Add Logo</h1>
                </div>
                <div>
                    <p className="text-gray-600">Maximum size should not exceed 200 kb</p>
                </div>
            </div>
            <div className="md:flex-1 bg-gray-200 flex flex-col justify-center items-center rounded-xl p-8 mt-2 md:ml-2 md:mt-0">
                <div className="mb-2">
                    <img src={SampleImg} alt=""/>
                </div>
                <div>
                    <h1 className="text-2xl font-bold">Add Cover Image</h1>
                </div>
                <div>
                    <p className="text-gray-600">Maximum size should not exceed 200 kb</p>
                </div>
            </div>
        </div>
    );
}
 
export default StepImage;